
@media all and (max-width:3000px) {

    #mainGroup {
        position: fixed;
        /* top: 10%;
        right: 5%; */
        width: 100%;
        height: 10%;
        padding: 1.3%;
        z-index: 6;
        border-bottom: 1px solid #efefef;
        background-color: white;
    }
    
    #mainMenu {
        float: right;
        margin-right: 16%;
        padding: 0.2%;
    }
      
    #mainMenu a {
        color: black;
        font-size: 15px;
        padding-right: 20px;
        cursor: pointer;
    }
    
    .clickedMenu {
        color: #2d3436;
    }
    
    .aboutIndazooLogo {
        float: left;
        padding-left: 16%;
        height: 100%;
        z-index: 4;
    }

}


@media all and (max-width:1000px) {
    
    #mainGroup {
        position: fixed;
        /* top: 10%;
        right: 5%; */
        width: 100%;
        height: 6%;
        padding: 1.3%;
        z-index: 6;
        border-bottom: 1px solid #efefef;
        background-color: white;
    }
    
    #mainMenu {
        float: right;
        margin-right: -4%;
        padding: 0.4%;
    }
      
    #mainMenu a {
        color: black;
        font-size: 15px;
        padding-right: 20px;
        cursor: pointer;
    }
    
    .clickedMenu {
        color: red;
    }
    
    .aboutIndazooLogo {
        float: left;
        padding-left: 0%;
        height: 100%;
        z-index: 4;
    }
}

