.members {
    position: absolute;
    left: 10%;
    right: 10%;
    top: 15%;
    bottom: 10%;
    padding-top: 5%;
    color: white;
    z-index: 150;
}

.members .member:first-child {
    position: absolute;
    width: 15%;
    left: 0%;
    top: 0%;
}

.members .member:nth-child(2) {
    position: absolute;
    width: 15%;
    left: 42.5%;
    top: 0%;
}

.members .member:nth-child(3) {
    position: absolute;
    width: 15%;
    left: 85%;
    top: 0%;
}

.members .member:nth-child(4) {
    position: absolute;
    width: 15%;
    left: 20%;
    top: 50%;
}

.members .member:nth-child(5) {
    position: absolute;
    width: 15%;
    left: 65%;
    top: 50%;
}

.member {
    color: white;
    margin-right: 10%;
}

.member img {
    width: 100%;
    height: 100%;
}

.carousel-caption {
    position: absolute;
    top: 5%;
    left: 5%;
    width: 10%;
}

.carousel-indicators {
    position: fixed;
    bottom: 5%;
}

.carousel-item {
    position: fixed;
    left: 0px;
    top: 0px;
    right: 0px;
    bottom: 0px;
}

.memberImg {
    /* Set rules to fill background */
    min-height: 100%;
    min-width: 1024px;
      
    /* Set up proportionate scaling */
    width: 100%;
    height: auto;
      
    /* Set up positioning */
    position: fixed;
    top: 0;
    left: 0;
}

.dropdown-toggle::after {
    content: unset;
}

.btn-primary:hover {
    color:black;
    background-color: unset;
    border-color:unset;
}

.btn-primary:not(:disabled):not(.disabled).active, .btn-primary:not(:disabled):not(.disabled):active, .show>.btn-primary.dropdown-toggle {
    color:black;
    background-color: unset;
    border-color:unset;
}

.btn-primary {
    color:black;
    background-color: unset;
    border-color:unset;
}

.btn-primary.focus, .btn-primary:focus {
    box-shadow: unset;
}

.btn {
    border: 0px solid transparent;
}

.btn-primary.focus, .btn-primary:focus {
    box-shadow: unset;
}

.btn-primary:not(:disabled):not(.disabled).active:focus, .btn-primary:not(:disabled):not(.disabled):active:focus, .show>.btn-primary.dropdown-toggle:focus {
    box-shadow: unset;
}

.artistEmoticon {
    margin-right: 5px;
}

#artistToggleIcon {
    display: inline-block;
    position: absolute;
    vertical-align: middle;
    right: 1px;
    cursor: pointer;
}

@media screen and (max-width: 1024px) { /* Specific to this particular image */
    .memberImg {
      left: 50%;
      margin-left: -512px;   /* 50% */
    }
    
  }