}


#playBtn {
  position: fixed;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  z-index: 100;
  font-size: 60px;
  color: #bdc3c7;
  cursor: pointer;
}

.mouseEnter {
  color: white;
}

#indazooLogo {
