  position: fixed;
  width: 10%;
  height: 10%;
  left: 10%;
  bottom: 10%;
  z-index: 5;
}

#iconGroup {
  position: fixed;
  bottom: 10%;
  right: 5%;
  z-index: 6;
}

#appMenu a {
  color: #2d3436;
  font-size: 20px;
  padding-right: 20px;
  cursor: pointer;
}

#appMenu a:last-child {
  padding-right: 0;
}

a:hover {
  text-decoration: none;
}

.carouselImg {
  /* Set rules to fill background */
  min-height: 100%;
  min-width: 1024px;
    
  /* Set up proportionate scaling */
  width: 100%;
  height: auto;
    
  /* Set up positioning */
  position: fixed;
  top: 0;
  left: 0;
}

.carousel-control-prev-icon {
  position: fixed;
  left: 5%;
  top: 50%; 
}

.carousel-control-next-icon {
  position: fixed;
  right: 5%;
  top: 50%; 
}

.carousel-indicators {
  display:none;
}

@media screen and (max-width: 1024px) { /* Specific to this particular image */
  .carouselImg {
    left: 50%;
    margin-left: -512px;   /* 50% */
  }
  
}

@media all and (max-width:3000px) {

    #mainGroup {
        position: fixed;
        /* top: 10%;
        right: 5%; */
        width: 100%;
        height: 10%;
        padding: 1.3%;
        z-index: 6;
        border-bottom: 1px solid #efefef;
        background-color: white;
    }
    
    #mainMenu {
        float: right;
        margin-right: 16%;
        padding: 0.2%;
    }
      
    #mainMenu a {
        color: black;
        font-size: 15px;
        padding-right: 20px;
        cursor: pointer;
    }
    
    .clickedMenu {
        color: #2d3436;
    }
    
    .aboutIndazooLogo {
        float: left;
        padding-left: 16%;
        height: 100%;
        z-index: 4;
    }

}


@media all and (max-width:1000px) {
    
    #mainGroup {
        position: fixed;
        /* top: 10%;
        right: 5%; */
        width: 100%;
        height: 6%;
        padding: 1.3%;
        z-index: 6;
        border-bottom: 1px solid #efefef;
        background-color: white;
    }
    
    #mainMenu {
        float: right;
        margin-right: -4%;
        padding: 0.4%;
    }
      
    #mainMenu a {
        color: black;
        font-size: 15px;
        padding-right: 20px;
        cursor: pointer;
    }
    
    .clickedMenu {
        color: red;
    }
    
    .aboutIndazooLogo {
        float: left;
        padding-left: 0%;
        height: 100%;
        z-index: 4;
    }
}


