#aboutBackground {
    background-color: #fafafa;
    position: fixed;
    left: 0px;
    top: 0px;
    right: 0px;
    bottom: 0px;
} 

#leftAboutBox {
    border: 1px solid #efefef;
    border-radius: 5px;
    position: absolute;
    left: 19%;
    top: 20%;
    width: 40%;
    height: 70%;
    background-color: white;
}

#rightAboutBox {
    border: 1px solid #efefef;
    border-radius: 5px;
    position: absolute;
    right: 19%;
    top: 20%;
    width: 20%;
    height: 70%;
    background-color: white;
}

#leftFirstBox {
    position: absolute;
    width: 100%;
    height: 11%;
    padding: 1%;
    text-align: left;
    border-bottom:1px solid #efefef;
}

#leftSecondBox {
    position: absolute;
    top: 11%;
    width: 100%;
    height: 89%;
    text-align: center;
}

#logoImg{
    display: inline-block;
    margin-left: 15px;
    margin-right: 15px;
}

#indazooFaceLogo {
    width: 40px;
    height: 40px;
}

#logoName {
    display: inline-block;
    vertical-align: middle;
    font-weight: bold;
}

#memberImg {
    position: absolute;
    width: 100%;
    height: 100%;
}

#memberImg img {
    width: 100%;
    height: 100%;
}

.rightlogoImg{
    display: inline-block;
    margin-left: 15px;
    margin-right: 15px;
}

.rightLogo {
    width: 40px;
    height: 40px;
}

.rightName {
    display: inline-block;
    vertical-align: middle;
    font-weight: bold;
}


.indazooMember {
    text-align: left;
    margin: 5%;
    margin-bottom: 35px;
}

a {
    color: black;
}

a:hover {
    color: rgb(255, 196, 20);
}